.word{
    border: 1px solid rgb(237, 239, 244);
    
}
.word:not(:last-child) {
    margin-bottom: 20px;
}
.worditem {
    
    padding: 15px;
    /* margin-bottom: 15px; */
    display: flex;
    justify-content: space-between;
}
.worditem p{
    width: 50%;
    padding: 5px 15px 5px 0px;
    display: flex;
    align-items: center;
    font-weight: 600;
}
.worditem p:nth-child(2){
    
    padding: 0 20px;
    border-left: 2px solid rgba(11, 8, 134, 0.25);
}
.worditem.checked p {
    text-decoration: line-through;
    color: rgba(3, 2, 64, 0.8);
}
.worditem__actions{
    display: flex;
    gap: 18px;
    align-items: center;
}
.worditem__actions input{
    width: 20px;
    height: 20px;
}
.worditem__btn{
    width: 24px;
    height: 24px;
    box-sizing: border-box;
    border: 1px solid rgba(243, 16, 16, 0.3);
    background-color: rgba(210, 196, 196, .3);
    border-radius: 50%;
    background-image: url('./../../assets/close-btn.png');
    background-size: 12px;
    background-repeat: no-repeat;
    background-position: center center;
    transition: all .3s ease;
}
.worditem__btn:hover{
    background-color: rgba(243, 16, 16, .8);   
}