.card {
  width: 400px;
  height: 420px;
  margin: 0 auto;
  padding: 40px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  perspective: 1000px; /* Добавляем перспективу для 3D эффекта */
  transform-style: preserve-3d; /* Определяем способ применения 3D трансформаций */
  transition: transform 0.5s; /* Добавляем анимацию поворота */
}

.card.translated {
  transform: rotateY(360deg); /* Поворачиваем карту при наличии класса translated */
}

.card__content {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card__content h2 {
  font-weight: 600;
  font-size: 18px;
}

.card__front,
.card__back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.card__front {
  z-index: 2;
}

.card__back {
  transform: rotateY(180deg);
}