.button {
    padding: 15px 24px;
    border-radius: 26px;
    background-color: rgba(66, 85, 255, 1);
    color: #fff;
    text-align: center;
    transition: all .3s ease;
    font-size: 15px;
}
.button:hover{
    box-shadow: 2px 2px 5px -2px rgba(0,0,0,0.5);
    background-color: rgba(66, 85, 255, .8);
}