.header{   
    padding: 25px 0;
    background-color: #fff;
}
.wrapper{
    width: 100%;
    position: relative;
}
.wrapper::before{
    content: "CARDS";
    width: 120px;
    height: 40px;
    font-size: 36px;
    font-family: "Open Sans", sans-serif;
    font-weight: 600;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
}
.form__inner{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 24px;
}

.form__input{
    border: 1px solid #EDEFF4;
    border-radius: 26px;
    width: 240px;
    padding: 15px 24px;
    font-size: 15px;
    color: rgba(3, 2, 64, 1);
    background-color: #F6F7FB;
}
.form__input:focus{
    box-shadow: 2px 2px 5px -2px rgba(0,0,0,0.5);
}
.form__input::placeholder{
    font-size: 15px;
    color: rgba(3, 2, 64, 0.7);
}
.invalid{
    border: 1px solid rgba(234, 18, 18, 1);
    background-color: rgba(234, 18, 18, 0.1);
}

.form__btn{
    border-radius: 50%;
    width: 49px;
    height: 49px;
    background-color: rgba(66, 85, 255, 1);
    transition: all .3s ease;
    background-image: url('./../../assets/add-btn.svg');
    background-size: 30px;
    background-repeat: no-repeat;
    background-position: center center;
}
.form__btn:hover{
    box-shadow: 2px 2px 5px -2px rgba(0,0,0,0.5);
    background-color: rgba(66, 85, 255, .8);    
}
.form__btn:focus{
    box-shadow: 2px 2px 5px -2px rgba(0,0,0,0.5);
}