.cards{
    position: relative;
    max-width: 400px;
    margin: 0 auto;
    margin-bottom: 30px;
}
.cards__btn{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border-radius: 50%;
    width: 49px;
    height: 49px;
    background-color: rgba(66, 85, 255, 1);
    transition: all .3s ease;
    background-size: 24px;
    background-repeat: no-repeat;   
}
.cards__prevBtn{
    background-image: url('./../../assets/arrow-left.png');
    left: -70px;
    background-position: left 12px center;
}
.cards__nextBtn{
    right: -70px;
    background-image: url('./../../assets/arrow-right.png');
    background-position: right 12px center;
}
.form__btn:hover{
    box-shadow: 2px 2px 5px -2px rgba(0,0,0,0.5);
    background-color: rgba(66, 85, 255, .8);    
}
.form__btn:focus{
    box-shadow: 2px 2px 5px -2px rgba(0,0,0,0.5);
}
.card__message{
    text-align: center;
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 30px;
}